import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppComponent} from './app.component';
import {HeaderComponent} from './index/header/header.component';
import {IndexComponent} from './index/index.component';
import {BroadcastComponent} from './index/broadcast/broadcast.component';
import {IndexRecommendComponent} from './index/index-recommend/index-recommend.component';
import {StarsComponent} from './stars/stars.component';
import {IndexCountToolComponent} from './index/index-count-tool/index-count-tool.component';
import {ChooseSpecComponent} from './index/choose-spec/choose-spec.component';
import {GoodsComponent} from './index/goods/goods.component';
import {CartComponent} from './index/cart/cart.component';
import {QrcodeComponent} from './index/qrcode/qrcode.component';
import {CommonLayerComponent} from './index/common-layer/common-layer.component';
import {ConfirmOrderComponent} from './confirm-order/confirm-order.component';
import {RouterModule, Routes} from "@angular/router";
import {FoodDetailComponent} from './food-detail/food-detail.component';
import {KSSwiperModule} from "angular2-swiper";
import {WelcomeComponent} from './welcome/welcome.component';
import { OrderListComponent } from './order-list/order-list.component';
import { PayComponent } from './pay/pay.component';
import { OrderDetailComponent } from './order-detail/order-detail.component';
import { SelectComponent } from './confirm-order/select/select.component';
import { AddFoodOrderComponent } from './confirm-order/add-food-order/add-food-order.component';
import { OrderSuccessComponent } from './order-success/order-success.component';
import { PaySuccessComponent } from './pay-success/pay-success.component';


const routeConfig: Routes = [
  {path: '', redirectTo: '/index', pathMatch: 'full'},
  {path: 'index', component: IndexComponent},
  {path: 'confirm-order', component: ConfirmOrderComponent},
  {path: 'confirm-order/add-food', component: AddFoodOrderComponent},
  {path: 'food-detail', component: FoodDetailComponent},
  {path: 'welcome', component: WelcomeComponent},
  {path: 'order-list', component: OrderListComponent},
  {path: 'order/:id', component: OrderDetailComponent},
  {path: 'order-success', component: OrderSuccessComponent},
  {path: 'pay-success', component: PaySuccessComponent}


]

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    IndexComponent,
    BroadcastComponent,
    IndexRecommendComponent,
    StarsComponent,
    IndexCountToolComponent,
    ChooseSpecComponent,
    GoodsComponent,
    CartComponent,
    QrcodeComponent,
    CommonLayerComponent,
    ConfirmOrderComponent,
    FoodDetailComponent,
    WelcomeComponent,
    OrderListComponent,
    PayComponent,
    OrderDetailComponent,
    SelectComponent,
    AddFoodOrderComponent,
    OrderSuccessComponent,
    PaySuccessComponent,
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routeConfig),
    KSSwiperModule
  ],
  providers: [],
  entryComponents: [CommonLayerComponent,PayComponent],
  bootstrap: [AppComponent]
})
export class AppModule {
}



