import {
  Component, ComponentFactory, ComponentFactoryResolver, ComponentRef, OnDestroy, OnInit, ViewChild,
  ViewContainerRef
} from '@angular/core';
import {PayComponent} from "../pay/pay.component";
import {Router} from "@angular/router";

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.css']
})
export class OrderListComponent implements OnInit, OnDestroy {

  @ViewChild('payLayerContainer',{read: ViewContainerRef})
  container: ViewContainerRef;

  @ViewChild('header')
  header;

  componentRef: ComponentRef<PayComponent>;

  constructor(private resolver: ComponentFactoryResolver,
              private router: Router) { }

  ngOnInit() {
  }

  doOrderQuery(event,type:number){
    var nav = document.querySelectorAll('.nav-item');
    for(let i = 0; i<nav.length; i++){
      nav[i].classList.remove('active');
    }
    event.target.parentNode.classList.add('active');
  }

  creatPayLayer(){
    const factory :ComponentFactory<PayComponent> = this.resolver.resolveComponentFactory(PayComponent);
    this.componentRef = this.container.createComponent(factory);
    this.componentRef.instance.closeCheck.subscribe((data) => {
      if(data){
        this.componentRef.destroy()
      }
    });
    this.componentRef.instance.commonLayerCheck.subscribe((data) => {
      this.componentRef.destroy()
      if(data === 0){
        this.header.creatCommonLayer(this.header.callLayerData)
      } else if (data === 1){
        this.header.creatCommonLayer(this.header.goDeskLayerData)
      }
    })
  }

  goIndex(){
    this.router.navigateByUrl('/index');
  }

  ngOnDestroy(){
    if(!!this.componentRef){
      this.componentRef.destroy();
    }
  }

}



