import {
  Component, ElementRef, OnInit, QueryList, Renderer2, ViewChild,
  ViewChildren
} from '@angular/core';
import {Router} from "@angular/router";

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {

  private _elem: ElementRef;
  private _renderer: Renderer2;

  cartFoods: Array<CartItem> = [
    new CartItem(1, "清蒸鲈鱼", true, 36, 1,[]),
    new CartItem(2, "红烧排骨", true, 30, 1,['小份','微辣']),
    new CartItem(1, "酸辣土豆丝", true, 18, 1,[]),
    new CartItem(1, "八宝酱菜", true, 12, 1,[])
  ]

  constructor(elem: ElementRef, renderer: Renderer2, public router: Router) {
    this._elem = elem.nativeElement;
    this._renderer = renderer;
  }

  @ViewChildren('remarkContent')
  elems: QueryList<ElementRef>;
  @ViewChild('innerList')
  elem: ElementRef;


  ngOnInit() {
  }


  showRemark(i) {
    this.elems.forEach((el, index) => {
      if (i == index) {
        el.nativeElement.style.display = "block";
        el.nativeElement.querySelector(".remark").focus();
      }
    });
    //
    // this.elem[i].querySelector(".remark").focus();
  }

  closeInnerCart() {  //关闭购物车
    this.elem.nativeElement.style.display = "none";
    // event.target.style.display = "none"
  }

  showInnerCart() { //展开购物车
    if(this.cartFoods.length>0){
      this.elem.nativeElement.style.display = "block";
    }
    // event.target.style.display = "none"
  }

  nav(url: string) {
    this.router.navigateByUrl(url);
  }

  allSelect() {
    let Bool = true;
    if (this.cartFoods) {
      this.cartFoods.forEach((item) => {
        if (item.isSelected == false) {
          Bool = false;
        }
      })
    }
    return Bool;
  }
  counterPlus(index){
    this.cartFoods[index].num++;
  }
  counterInc(index){
    this.cartFoods[index].num--;
    if(this.cartFoods[index].num === 0 ){
      this.cartFoods.splice(index,1);
    }
  }

  clearCart(){  //清空购物车
    this.cartFoods = [];
    this.closeInnerCart();
  }

  toggleSelect(index:number){ //购物车单选
    this.cartFoods[index].isSelected = !this.cartFoods[index].isSelected;
  }

  toggleSelectAll(){ //购物车全选
    if(this.allSelect()){
      this.cartFoods.forEach((item) => {
        item.isSelected = false;
      })
    }else {
      this.cartFoods.forEach((item) => {
        item.isSelected = true;
      })
    }

  }

}

export class CartItem {
  constructor(public foodId: number,
              public name: string,
              public isSelected: boolean,
              public price: number,
              public num: number,
              public attrs: Array<string>
  ) {

  }
}



