import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Spu} from "../index-recommend/index-recommend.component";

@Component({
  selector: 'app-index-count-tool',
  templateUrl: './index-count-tool.component.html',
  styleUrls: ['./index-count-tool.component.css']
})
export class IndexCountToolComponent implements OnInit {

  @Input()
  card: Spu = new Spu(1, "爆炒肥肠", "/assets/img/card_img.jpg", 5, 0, 25, {isSpec: true,specs: [{specId: 11,specTitle:"规格", specItems: [{itemId:111,itemDesc:"小份（2-3人）",itemNum:0,itemPrice:55},{itemId:112,itemDesc:"中份（4-5人）",itemNum:0,itemPrice:75}]}]});
  @Output()
  cardChange: EventEmitter<Spu> = new EventEmitter();

  @Input()
  isSpecShow: boolean;
  @Output()
  isSpecShowChange: EventEmitter<boolean> = new EventEmitter()

  constructor() {
  }

  ngOnInit() {
  }

  counterAdd() {
    this.card.num++;
    this.cardChange.emit(this.card)
  }
  counterInc() {
    this.card.num--;
    this.cardChange.emit(this.card)
  }

  showSpec(card: Spu){
    this.isSpecShow = true;
    this.isSpecShowChange.emit(this.isSpecShow);
  }
}



