import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-common-layer',
  templateUrl: './common-layer.component.html',
  styleUrls: ['./common-layer.component.css']
})
export class CommonLayerComponent implements OnInit {

  @Input()
  layerData: LayerData;
  @Output()
  returnResult: EventEmitter<LayerData> = new EventEmitter();



  constructor(
  ) { }

  ngOnInit() {
  }

  layerReturn(index){
    if(index === 0 ){
      this.layerData.layerReturn = true;
    } else if(index === 1){
      this.layerData.layerReturn = false;
    } else{
      console.log("error index");
      return;
    }
    this.returnResult.emit(this.layerData);
  }

}

export class LayerData{
  constructor(
    public layerText: string,
    public layerOptions: Array<string>,
    public layerOrigin: string,
    public layerReturn: boolean
  ){

  }
}



