import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'app-index-recommend',
  templateUrl: './index-recommend.component.html',
  styleUrls: ['./index-recommend.component.css']
})
export class IndexRecommendComponent implements OnInit {

  @Input()
  spus: Array<Spu>;

  isSpecShow: boolean = false;

  listWidth: string;

  constructor() { }

  ngOnInit() {
    console.log(this.spus)
    this.listWidth = (this.spus.length*140) + "px";
  }

  logger(){
    console.log(this.spus)
  }

}

export class Spu {
  constructor(public id: number,
              public name: string,
              public picture: string,
              public rating: number,
              public num: number,
              public price: number,
              public spec: Spec) {
  }

}

export class Spec {
  constructor(public isSpec: boolean) {
  }

  public specs: Array<Specs>
}

export class Specs {
  constructor(public specId: number,
              public specTitle: string,
              public specItems: Array<SpecItem>) {
  }
}
export class SpecItem {
  constructor(public itemId: number,
              public itemDesc: string,
              public itemNum: number,
              public itemPrice: number) {

  }
}



