import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Spu} from "../index-recommend/index-recommend.component";

@Component({
  selector: 'app-choose-spec',
  templateUrl: './choose-spec.component.html',
  styleUrls: ['./choose-spec.component.css']
})
export class ChooseSpecComponent implements OnInit {

  @Input()
  spu: Spu =  new Spu(1, "爆炒肥肠", "/assets/img/card_img.jpg", 5, 0, 25, {isSpec: true,specs: [{specId: 11,specTitle:"规格", specItems: [{itemId:111,itemDesc:"小份（2-3人）",itemNum:0,itemPrice:55},{itemId:112,itemDesc:"中份（4-5人）",itemNum:0,itemPrice:75}]}]});
  @Output()
  spuChange: EventEmitter<Spu> = new EventEmitter();

  @Input()
  isSpecShow: boolean;
  @Output()
  isSpecShowChange: EventEmitter<boolean> = new EventEmitter();

  constructor() { }

  ngOnInit() {
    console.log(this.spu)
  }
  counterAdd() {
    this.spu.num++;
    this.spuChange.emit(this.spu)
  }
  counterInc() {
    this.spu.num--;
    this.spuChange.emit(this.spu)
  }

  closeLayer(){
    this.isSpecShow = false;
    this.isSpecShowChange.emit(this.isSpecShow);
    console.log(this.isSpecShow)
  }

}



