import { Component, OnInit } from '@angular/core';
declare var $:any;
declare var MobileSelectArea:any;
@Component({
  selector: 'app-select',
  templateUrl: './select.component.html',
  styleUrls: ['./select.component.css']
})
export class SelectComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    //提交选择月份
    var json =[];
    for(let i=1;i<99;i++){
      json.push({id:i,name:i});
    }
    var selectArea2 = new MobileSelectArea();
    selectArea2.init({trigger:'#zfbtn',level:1,value:$('#zfbtn').data('value'),data:json,eventName:'focus'});
    $('.num-change').click(function(){
        $('#zfbtn').focus();

      }
    )
  }


}



