import {
  Component, ComponentFactory, ComponentFactoryResolver, ComponentRef, OnDestroy, OnInit, ViewChild,
  ViewContainerRef
} from '@angular/core';
import {CommonLayerComponent, LayerData} from "../common-layer/common-layer.component";
import {Location, LocationStrategy, PathLocationStrategy} from "@angular/common";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  providers: [Location, {provide: LocationStrategy, useClass: PathLocationStrategy}]
})
export class HeaderComponent implements OnInit, OnDestroy {

  noPayLayerData: LayerData = {
    layerText: '客官，您还有未结账的订单哦～',
    layerOptions: ['查看订单'],
    layerOrigin: 'noPay',
    layerReturn: null
  }

  goDeskLayerData: LayerData = {
    layerText: '客官，就几步路，劳驾您去前台付款哦！',
    layerOptions: ['我这就去'],
    layerOrigin: 'goDesk',
    layerReturn: null
  }

  callLayerData: LayerData = { //呼叫服务员
    layerText: '客官，需要我们的服务员前来为您提供服务吗？',
    layerOptions: ['是的', '我点错了'],
    layerOrigin: 'call',
    layerReturn: null
  }
  callOverLayerData: LayerData = {  //确定呼叫服务员
    layerText: '客官，我们的服务员已经收到了您的召唤，正火速赶来～',
    layerOptions: ['好的，辛苦啦！'],
    layerOrigin: 'callOver',
    layerReturn: null
  }

  title: string;

  @ViewChild('layerContainer', {read: ViewContainerRef})
  container: ViewContainerRef;

  componentRef: ComponentRef<CommonLayerComponent>;

  isIndex: boolean = true;
  btnStatus = {
    goBack: false,
    refresh: false,
    call: false,
    order: false
  }

  constructor(private resolver: ComponentFactoryResolver,
              private location: Location,) {
  }

  ngOnInit() {

    //判断当前页面
    if (this.location.path().startsWith('/index')) {
      this.title = '3号桌';
      this.btnStatus.goBack = false;
      this.btnStatus.refresh = true;
      this.btnStatus.call = true;
      this.btnStatus.order = true;
      this.isIndex = true;
    } else if (this.location.path().startsWith('/confirm-order')) {
      this.title = '确认订单';
      this.btnStatus.goBack = true;
      this.btnStatus.refresh = false;
      this.btnStatus.call = false;
      this.btnStatus.order = false;
      this.isIndex = false;
    } else if (this.location.path().startsWith('/order-list')) {
      this.title = '我的订单';
      this.btnStatus.goBack = true;
      this.btnStatus.refresh = false;
      this.btnStatus.call = false;
      this.btnStatus.order = false;
      this.isIndex = false;
    } else if (this.location.path().startsWith('/order/')) {
      this.title = '本单详情';
      this.btnStatus.goBack = true;
      this.btnStatus.refresh = false;
      this.btnStatus.call = false;
      this.btnStatus.order = false;
      this.isIndex = false;
    }
    if (this.isIndex) {
      this.creatCommonLayer(this.noPayLayerData);
    }
  }

  goBack() {
    this.location.back();
  }

  doRefresh() {
    window.location.reload();
  }

  creatCommonLayer(data) {
    this.container.clear();  //清除容器中原有组件
    const factory: ComponentFactory<CommonLayerComponent> = this.resolver.resolveComponentFactory(CommonLayerComponent);
    this.componentRef = this.container.createComponent(factory);
    this.componentRef.instance.layerData = data;
    this.componentRef.instance.returnResult.subscribe((data) => {

      switch (data.layerOrigin) {
        case 'call': //如果返回数据来自 "呼叫服务员"
          if (!data.layerReturn) {
            this.container.clear();
          } else if (data.layerReturn) {
            //发送呼叫服务员请求
            this.creatCommonLayer(this.callOverLayerData);
          }
          break;
        case 'callOver': //如果数据来自 "确定呼叫服务员"
          this.container.clear();
          break;
        case 'goDesk': //数据来自"去前台结账"
          this.container.clear();
        default:
          this.container.clear();
      }


    })

  }

  //组件销毁
  ngOnDestroy() {
    if (!!this.componentRef) {
      this.componentRef.destroy();
    }
  }

}



