import {Component, ElementRef, OnInit, QueryList, ViewChildren} from '@angular/core';
import {OrderItem} from "../confirm-order.component";
import {Router} from "@angular/router";

declare var $: any;

@Component({
  selector: 'app-add-food-order',
  templateUrl: './add-food-order.component.html',
  styleUrls: ['./add-food-order.component.css']
})
export class AddFoodOrderComponent implements OnInit {

  cartFoods: Array<OrderItem> = [
    new OrderItem(1, "清蒸鲈鱼", true, 36, 1, [], false),
    new OrderItem(2, "红烧排骨", true, 30, 1, ['小份', '微辣'], false),
    new OrderItem(1, "酸辣土豆丝", true, 18, 1, [], false),
    new OrderItem(1, "八宝酱菜", true, 12, 1, [], false)
  ]

  @ViewChildren('remarkContent')
  elems: QueryList<ElementRef>;

  constructor(private router: Router) {
  }

  ngOnInit() {
    $('.ordered-list-toggle').click(
      function () {
        $('.ordered-list').slideToggle()
      }
    )
  }

  //菜品数量加减
  counterPlus(index) {
    this.cartFoods[index].num++;
  }

  counterInc(index) {
    if (this.cartFoods[index].num === 0) {
      return;
    }
    this.cartFoods[index].num--;
  }

  //判断是否全打包
  isAllPack() {
    let i = true;
    this.cartFoods.forEach((item) => {
      if (item.isPacked === false) {
        i = false
      }
    })
    return i;
  }

  //全部打包
  togglePackAll() {
    if (this.isAllPack()) {
      this.cartFoods.forEach((item) => {
        item.isPacked = false;
      })
    } else {
      this.cartFoods.forEach((item) => {
        item.isPacked = true;
      })
    }
  }

  //单独打包
  togglePacked(index) {
    console.log(index);
    this.cartFoods[index].isPacked = !this.cartFoods[index].isPacked;
  }

  //添加备注
  showRemark(i) {
    this.elems.forEach((el) => {
      // console.log(el.nativeElement.dataset.hashid)
      if (i == el.nativeElement.dataset.hashid) {
        el.nativeElement.style.display = "block";
        el.nativeElement.querySelector(".remark").focus();
      }
    });
    //
    // this.elem[i].querySelector(".remark").focus();
  }

  //隐藏备注
  hiddenRemark(event) {
    if (event.target.value !== '') {
      return;
    }
    setTimeout(() => {
      event.target.parentNode.style.display = 'none';
    }, 50)

  }

  // 提交订单
  nav() {
    //提交数据
    this.router.navigateByUrl('/order-success');
  }

}



